export default function (Component) {
      if(!Component.options.code) Component.options.code = {};
      Component.options.code['use'] = "const node = editor.nodes[0];\nconst control = node.controls.get('ctrl');\n\nnode.update(); // принудительное обновление представления\ncontrol.update();\n\n// в некоторых случаях вам может потребоваться Vue.js контекст\nnode.vueContext\ncontrol.vueContext";
    }